<template>
  <div>
    <section id="two" class="wrapper style2">
      <div class="inner">
        <div class="box">
          <div class="content">
            <div class="table-wrapper">
              <table>
                <thead>
                  <th :style="{ width: '15%' }">번호</th>
                  <th :style="{ width: '50%' }">제목</th>
                  <th :style="{ width: '10%' }">작성자</th>
                  <th :style="{ width: '25%' }">날짜</th>
                </thead>
                <tbody>
                  <td>{{ no }}</td>
                  <td>{{ title }}</td>
                  <td>{{ userId }}</td>
                  <td>{{ getFormatDate(date) }}</td>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="4">{{content}}</td>
                  </tr>
                </tfoot>
              </table>
            </div>
            <hr />

            <div align="center" style="padding-top:50px;">
              <router-link :to="'/notice/update?no=' + no">
                <a class="button special" v-if="sessionid==userId" style="margin-right:20px;">수정</a>
              </router-link>
              <router-link :to="'/notice/delete?no=' + no">
                <a class="button" v-if="sessionid==userId" style="margin-right:20px;">삭제</a>
              </router-link>
              <router-link to="/notice/list">
                <a class="button alt">목록</a>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import moment from "moment";
import store from "@/store/store.js";

export default {
  name: "detail",
  props: {
    no: { type: Number },
    userId: { type: String },
    title: { type: String },
    content: { type: String },
    date: { type: String },
  },
  data: function() {
    return {
      sessionid: "",
    };
  },
  /* methods: {
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }, */
  methods: {
    getFormatDate(date) {
      return moment(new Date(date)).format("YYYY.MM.DD HH:mm:ss");
    },
    moveList() {
      this.$router.push("/notice/list");
    }
  },


  created() {
    this.sessionid = store.state.userid;
  }
};
</script>