<template>
  <tr>
    <td>{{ no }}</td>
    <td>
      <router-link :to="`/notice/read?no=${no}`">{{ title }}</router-link>
    </td>
    <td>{{ userId }}</td>
    <td>{{ getFormatDate(date) }}</td>
  </tr>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    no: { type: Number },
    userId: { type: String },
    title: { type: String },
    date: { type: String },
  },
  methods: {
    getFormatDate(date) {
      return moment(new Date(date)).format('YYYY.MM.DD');
    },
  },
};
</script>