import Vue from "vue";
import Vuex from "vuex";
import http from '@/util/http-common';
import {router} from '@/router/index.js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    items: [],
    item: {},
    userid:"",
  }, 
  getters: {
    items(state) {
      return state.items;
    },
    item(state) {
      return state.item;
    },
    userid(state) { 
      return state.userid;
    },
  },
  mutations: {
    mutateSetItems(state, items){
      state.items = items
    },
    mutateSetItem(state, item){
      state.item = item
    },
    mutateSetUserid(state, userid){
      state.userid = userid
    },
  },
  actions: {
    getItems(context) {
      http
        .get('/notice')
        .then(({ data }) => {
          context.commit('mutateSetItems', data);
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },
    getItem(context, payload) {
      http.get(payload).then(({ data }) => {
        context.commit('mutateSetItem', data);
      });
    },
    getUserid(context,{url}) {
      http
        .get('/notice/id')
        .then(({ data }) => {
          context.commit('mutateSetUserid', data);
          router.push(url);
        })
        .catch(() => {
          alert('에러가 발생했습니다.');
        });
    },
  }
});
