<template>
  <div>
    <section id="two" class="wrapper style2">
      <div class="inner">
        <div class="box">
          <!-- <div class="content"> -->
          <div class="content">
            <header class="align-center">
              <h2>공지사항 등록</h2>
            </header>
            <div class="row uniform">
              <h3>제목</h3>
              <div class="12u$" style="padding-top:0;">
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  ref="title"
                  placeholder="제목을 입력하세요"
                  v-model="title"
                />
              </div>

              <h3>내용</h3>
              <div class="12u$" style="padding-top:0;">
                <textarea
                  type="text"
                  class="form-control"
                  id="content"
                  ref="content"
                  rows="10"
                  placeholder="내용을 입력하세요"
                  v-model="content"
                ></textarea>
              </div>
            </div>
          </div>
            <div align="center" style="padding-bottom:50px;">
              <a class="button special" v-if="type == 'create'" @click="checkHandler" style="margin-right:20px;">등록</a>
              <a class="button special" v-if="type == 'update'" @click="checkHandler" style="margin-right:20px;">수정</a>
              <a class="button alt" @click="moveList">목록</a>
            </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import http from "@/util/http-common";
import store from "@/store/store.js";

export default {
  name: "notice-Form",
  props: {
    type: { type: String }
  },
  data: function() {
    return {
      no: "",
      userId: "",
      title: "",
      content: "",
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";

      !this.title &&
        ((msg = "제목 입력해주세요"),
        (err = false),
        this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else this.type == "create" ? this.createHandler() : this.updateHandler();
    },
    createHandler() {
      http
        .post("/notice", {
          userId: this.userId,
          //qnaUserid: "admin",
          title: this.title,
          content: this.content
        })
        .then(({ data }) => {
          let msg = "등록 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "등록이 완료되었습니다.";
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert("등록 처리시 에러가 발생했습니다.");
        });
    },
    updateHandler() {
      http
        .put(`/notice/${this.no}`, {
          no: this.no,
          userId: this.userId,
          title: this.title,
          content: this.content
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }

          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert("수정 처리시 에러가 발생했습니다.");
        });
    },
    moveList() {
      this.$router.push("/notice/list");
    }
  },
  created() {
    this.userId = store.state.userid;
    if (this.type === "update") {
      http
        .get(`/notice/${this.$route.query.no}`)
        .then(({ data }) => {
          this.no = data.no;
          this.userId = data.userId;
          this.title = data.title;
          this.content = data.content;
        })
        .catch(() => {
          alert("에러가 발생했습니다.");
        });
    }
  }
};
</script>

<style scoped></style>
