<template>
  <section id="two" class="wrapper style4" :style="{height: '800px'}">
    <div class="inner">
      <div class="box" align="center">
        <!-- <div class="content"> -->
        <div class="12u 12u$(medium)">
          <div class="table-wrapper" v-if="items.length">
            <table>
              <thead>
                <th :style="{ width: '15%' }">번호</th>
                <th :style="{ width: '50%' }">제목</th>
                <th :style="{ width: '10%' }">작성자</th>
                <th :style="{ width: '25%' }">날짜</th>
              </thead>
              <tbody>
                <list-row
                  v-for="(item, index) in items" 
                  :key="`${index}_items`"
                  :no="item.no"
                  :title="item.title"
                  :userId="item.userId" 
                  :date="item.date"
                />
              </tbody>
            </table>
          </div>
          <div v-else>공지사항이 없습니다.</div>
          <div style="text-align:right;" v-show="sessionid==='admin'">
            <!-- 관리자만 등록가능 -->
            <a class="button special" @click="movePage">등록</a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>  
// http from "@/util/http-common";
import store from "@/store/store.js";
import { mapGetters } from 'vuex';
import ListRow from "@/components/Row.vue";

export default {
  name: "list",
  components: {
    ListRow,
  },
  data: function() {
    return {
      sessionid: "",
    };
  },
  computed: {
    ...mapGetters(['items']),
  },
  created() {
    this.sessionid = store.state.userid;
    store.dispatch('getItems'); //action을 dispatch로 호출
  },
  methods: {
    movePage() {
      this.$router.push("/notice/create");
    }
  }
};
</script>

