import Vue from 'vue';
import VueRouter from 'vue-router';
import List from '@/page/list.vue';
import Create from '@/page/create.vue';
import Read from '@/page/read.vue';
import Update from '@/page/update.vue';
import Delete from '@/page/delete.vue';

Vue.use(VueRouter);
export const router = new VueRouter({
  mode: 'history',
  routes: [
    // {
    //   path: '/',
    //   name: 'index',
    //   component: Index,
    //   redirect:'/qna/list',
    // },
    {
      path: '/notice/list',
      name: 'list',
      component: List,
    },
    {
      path: '/notice/create',
      name: 'create',
      component: Create,
    },
    {
      path: '/notice/read',
      name: 'read',
      component: Read,
    },
    {
      path: '/notice/update',
      name: 'update',
      component: Update,
    },
    {
      path: '/notice/delete',
      name: 'delete',
      component: Delete,
    },
  ],
});
